import { Button, Input, Layout, Menu, Typography, Dropdown } from "antd";
import React from "react";
import styles from "./Header.module.css";
import logo from "../../assets/logo.svg";
import { GlobalOutlined } from "@ant-design/icons";
import { useNavigate, Link } from "react-router-dom";
import store from "../../redux/store";
import { LanguageState } from "../../redux/languageReducer";

interface MyComponentProps {
  navigate: ReturnType<typeof useNavigate>;
}

// tab 数据列表
const list = [
  { url: "/", title: "旅游首页" },
  { url: "/page1", title: "周末游" },
  { url: "/page1", title: "跟团游" },
  { url: "/page1", title: "自由行" },
  { url: "/page1", title: "私家团" },
  { url: "/page1", title: "邮轮" },
  { url: "/page1", title: "酒店+景点" },
  { url: "/page1", title: "当地玩乐" },
  { url: "/page1", title: "主题游" },
  { url: "/page1", title: "定制游" },
  { url: "/page1", title: "游学" },
  { url: "/page1", title: "签证" },
  { url: "/page1", title: "企业游" },
  { url: "/page1", title: "高端游" },
  { url: "/page1", title: "爱玩户外" },
  { url: "/page1", title: "保险" },
];

class HeaderClass extends React.Component<MyComponentProps, LanguageState> {
  constructor(prop) {
    super(prop);
    const storeState = store.getState().languageReducer;
    this.state = {
      language: storeState.language,
      languageList: storeState.languageList,
    };
    store.subscribe(this.handleStoreChang);
  }
  handleStoreChang = () => {
    const storeState = store.getState().languageReducer;
    this.setState({
      language: storeState.language,
    });
  };
  menuClickHandler = (e) => {
    if (e.key === "new") {
      const action = {
        type: "add_language",
        payload: {
          label: "新语言",
          key: "new_language",
        },
      };
      store.dispatch(action);
    } else {
      const action = {
        type: "change_language",
        payload: e.key,
      };
      store.dispatch(action);
    }
  };
  render() {
    const { navigate } = this.props;
    return (
      <div className={styles["app-header"]}>
        {/* top-header */}
        <div className={styles["top-header"]}>
          <div className={styles.inner}>
            <Typography.Text style={{ width: "150px" }}>
              让旅游更幸福
            </Typography.Text>

            <Dropdown.Button
              menu={{
                items: [
                  ...this.state.languageList!,
                  { label: "添加新语言", key: "new" },
                ],
                onClick: this.menuClickHandler,
              }}
              placement="bottomRight"
              icon={<GlobalOutlined />}
            >
              {this.state.language === "zh" ? "中文" : "English"}
            </Dropdown.Button>

            <Button.Group className={styles["button-group"]}>
              <Button onClick={() => navigate("/login")}>注册</Button>
              <Button onClick={() => navigate("/login")}>登陆</Button>
            </Button.Group>
          </div>
        </div>
        <Layout.Header className={styles["main-header"]}>
          <img src={logo} alt="logo" className={styles["App-logo"]} />
          <Link to={"/"}>
            <Typography.Title level={3} className={styles.title}>
              React 旅游网
            </Typography.Title>
          </Link>
          <Input.Search
            placeholder={"请输入旅游目的地、主题、或关键字"}
            className={styles["search-input"]}
          />
        </Layout.Header>
        <Menu mode="horizontal" className={styles["main-menu"]}>
          {list.map((item, index) => {
            return (
              <Menu.Item key={index + ""} onClick={() => navigate(item.url)}>
                {item.title}
              </Menu.Item>
            );
          })}
        </Menu>
      </div>
    );
  }
}

export const Header: React.FC = () => {
  const navigate = useNavigate();
  return <HeaderClass navigate={navigate} />;
};
