import styles from "./App.module.css";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Navigate,
} from "react-router-dom";
import { Home, Login, Err404, HomePage, Page1 } from "./view";

import { useEffect } from "react";

function Title({ title, el }) {
  useEffect(() => {
    document.title = title;
    return () => {
      document.title = "React 旅游网";
    };
  }, [title]);
  return el;
}

// 路由守卫组件 isAuthenticated 判断当前是否符合条件
function PrivateRoute({ isAuthenticated }) {
  return isAuthenticated ? <Home /> : <Navigate to="/login" />;
}

function App() {
  return (
    <Router>
      <div className={styles.App}>
        <Routes>
          <Route path={"/"} element={<PrivateRoute isAuthenticated={true} />}>
            <Route
              path="/"
              element={<Title title={"旅游首页"} el={<HomePage />} />}
            />
            <Route
              path="/page1"
              element={<Title title={"page1"} el={<Page1 />} />}
            />
          </Route>
          <Route
            path={"/login"}
            element={<Title title={"登录注册页面"} el={<Login />} />}
          />
          <Route path="*" element={<Err404 />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;
